import React from 'react';
import { Card, Grid, CardActionArea, CardContent, useTheme, styled } from '@mui/material';
import { Link as GatsbyLink } from 'gatsby';
import { Post } from 'src/models/fragments';

interface Props {
  prev?: Post | null;
  next?: Post | null;
  onClickNext?: () => void;
  onClickPrev?: () => void;
}
const Navigation = ({ prev, next, onClickNext, onClickPrev }: Props) => {
  const { spacing, palette } = useTheme();

  return (
    <Grid
      container
      component="nav"
      spacing={2}
      wrap="wrap"
      sx={{
        justifyContent: 'space-between',
      }}
    >
      {prev ? (
        <Grid item xs={12} lg={6}>
          <Card
            sx={{
              marginBottom: spacing(2),
              background: 'transparent',
              '& .MuiCardContent-root': {
                whiteSpace: 'nowrap',
                textOverflow: 'ellipsis',
                overflow: 'hidden',
              },
            }}
            variant="outlined"
          >
            <Link to={prev.fields?.path ?? '/'} rel="prev" onClick={onClickPrev}>
              <CardActionArea>
                <CardContent>{prev.frontmatter?.title}</CardContent>
              </CardActionArea>
            </Link>
          </Card>
        </Grid>
      ) : (
        <Grid />
      )}
      {next ? (
        <Grid item xs={12} lg={6}>
          <Card
            sx={{
              marginBottom: spacing(2),
              background: 'transparent',
              '& .MuiCardContent-root': {
                whiteSpace: 'nowrap',
                textOverflow: 'ellipsis',
                overflow: 'hidden',
                textAlign: 'right',
              },
            }}
            variant="outlined"
          >
            <Link to={next.fields?.path ?? '/'} rel="next" onClick={onClickNext}>
              <CardActionArea>
                <CardContent>{next.frontmatter?.title}</CardContent>
              </CardActionArea>
            </Link>
          </Card>
        </Grid>
      ) : (
        <Grid />
      )}
    </Grid>
  );
};

const Link = styled(GatsbyLink)(({ theme: { palette } }) => ({
  color: palette.text.secondary,
  textDecoration: 'none',
}));

export default Navigation;
