import React from 'react';
import { useProfileInfo } from 'hooks/useProfileInfo';
import { useSiteMetadata } from 'hooks/useSiteMetadata';
import { Post } from 'models/fragments';
import { getSrc } from 'gatsby-plugin-image';

const toIsoDate = (dateString: string | null | undefined) => {
  if (dateString == null || dateString === '') {
    return undefined;
  }

  const parsed = new Date(dateString);

  if (Number.isNaN(parsed.getTime())) {
    return undefined;
  }

  return parsed.toISOString();
};

const StructuredPostData = ({ post, pathname, lang }: { post: NonNullable<Post>; pathname: string; lang: string }) => {
  const profile = useProfileInfo();
  const siteMetadata = useSiteMetadata();

  if (profile == null) {
    return null;
  }

  const siteUrl = siteMetadata.siteUrl;
  const canonicalUrl = new URL(pathname, siteUrl).toString();

  const thumbnail = post.frontmatter?.thumbnail?.childImageSharp;
  const thumbnailSrc = thumbnail == null ? undefined : getSrc(thumbnail);
  const thumbnailUrl = thumbnailSrc == null ? undefined : new URL(thumbnailSrc, siteUrl).toString();
  const isoPublishedDate = toIsoDate(post.frontmatter?.date);
  const authorName = profile.site?.siteMetadata?.author?.name;
  const publisherLogo = new URL('/logo.png', siteUrl).toString();

  const data = {
    '@context': 'https://schema.org',
    '@type': 'BlogPosting',
    mainEntityOfPage: {
      '@type': 'WebPage',
      '@id': canonicalUrl,
    },
    author: {
      '@type': 'Person',
      name: authorName,
    },
    publisher: {
      '@type': 'Organization',
      name: siteMetadata.title,
      logo: {
        '@type': 'ImageObject',
        url: publisherLogo,
      },
    },
    headline: post.frontmatter?.title,
    ...(thumbnailUrl != null ? { image: [thumbnailUrl] } : {}),
    ...(isoPublishedDate != null ? { datePublished: isoPublishedDate, dateModified: isoPublishedDate } : {}),
    inLanguage: lang,
    url: canonicalUrl,
  };

  return <script type="application/ld+json" dangerouslySetInnerHTML={{ __html: JSON.stringify(data) }} />;
};

export default StructuredPostData;
