import { useStaticQuery, graphql } from 'gatsby';
import { useLanguage } from 'hooks/useLanguage';

export const useAllPosts = (ignoreLanguage = true) => {
  const { language: lang } = useLanguage();
  const data = useStaticQuery<Queries.AllPostsQuery>(graphql`
    query AllPosts {
      allMarkdownRemark(sort: { frontmatter: { date: DESC } }, filter: { fields: { mdType: { eq: "posts" } } }) {
        edges {
          node {
            ...Post
          }
        }
      }
    }
  `);

  const frontmatters = data.allMarkdownRemark.edges
    .map(({ node }) => node)
    .filter(p => (ignoreLanguage ? p.fields?.lang === lang : true));

  return frontmatters;
};
